import React from "react";

interface LogEntry {
  time: string;
  message: string;
}

interface TerminalScreenProps {
  currentUrl: string | null;
  logs: LogEntry[];
  setCurrentUrl: React.Dispatch<React.SetStateAction<string | null>>;
  setLogs: React.Dispatch<React.SetStateAction<LogEntry[]>>;
}

export default function TerminalScreen(
  terminalScreenProps: TerminalScreenProps
) {
  React.useEffect(() => {
    terminalScreenProps.setCurrentUrl(window.location.href);
  }, [1]);
  React.useEffect(() => {
    const handler = (e: Event) => {
      const customEvent = e as CustomEvent<string>;
      terminalScreenProps.setLogs((prevLogs) => [
        ...prevLogs,
        {
          time: new Date().toLocaleTimeString("en-GB", {
            hour: "2-digit",
            minute: "2-digit",
            second: "2-digit",
          }),
          message: customEvent.detail,
        },
      ]);
    };
    window.addEventListener("terminal-log", handler);
    return () => window.removeEventListener("terminal-log", handler);
  }, []);
  return (
    <div className="px-10 text-text text-xs overflow-y-scroll max-h-full h-full">
      <div className="text-text pt-10 ">
        Five Server <span className="text-cyan-200">running at:</span>
      </div>
      <div>
        {"> "}Network :{" "}
        <span className="text-highlight-green">
          {terminalScreenProps.currentUrl}
        </span>
      </div>
      {terminalScreenProps.logs.map((log, i) => (
        <p key={i} className="text-xs">
          <span className="text-text-gray text-[0.7rem]">{log.time + " "}</span>
          <span className="text-cyan-200">[File Server]</span>
          <span className="text-highlight-green">{" " + log.message}</span>
        </p>
      ))}
    </div>
  );
}
