import React from "react";
import ProblemsScreen from "./problemsScreen/page";
import TerminalScreen from "./terminalScreen/page";
import { MdFullscreen } from "react-icons/md";
import { IoClose } from "react-icons/io5";

interface LogEntry {
  time: string;
  message: string;
}
interface TerminalProps {
  bottomPannelToggle: boolean;
  setBottomPannelToggle: React.Dispatch<React.SetStateAction<boolean>>;
}

function Terminal({
  bottomPannelToggle,
  setBottomPannelToggle,
}: TerminalProps) {
  const [activeTab, setActiveTab] = React.useState("PROBLEMS");
  const [currentUrl, setCurrentUrl] = React.useState<string | null>(null);
  const [logs, setLogs] = React.useState<LogEntry[]>([]);
  const tabs: string[] = ["PROBLEMS", "TERMINAL", " OUTPUT"];

  return (
    <div
      className={
        bottomPannelToggle
          ? "w-screen h-48 fixed bottom-0 right-0 bg-secondary-bg animate-[slideUp_0.2s_ease-out_forwards] ease-in-out duration-300 "
          : "hidden"
      }
    >
      <div className=" flex items-center w-screen h-8 bg-[var(--bg)] fixed pl-10 pr-4 text-sm ">
        <div className="flex-grow flex">
          {tabs.map((tab: string) => (
            <p
              className={`text-gray text-sm mr-8 ${
                activeTab == tab
                  ? "border-b text-highlight-green border-highlight-green pb-1"
                  : ""
              } cursor-pointer hover:text-white`}
              key={tabs.indexOf(tab)}
              onClick={() => {
                setActiveTab(tab);
              }}
            >
              {tab}
            </p>
          ))}
        </div>
        <div className="flex items-center space-x-2">
          <MdFullscreen size={"20px"} color="white" />
          <IoClose
            size={"16px"}
            color="white"
            onClick={() => {
              setBottomPannelToggle(false);
              
            }}
          />
        </div>
      </div>
      {activeTab === "PROBLEMS" && <ProblemsScreen />}
      {activeTab === "TERMINAL" && (
        <TerminalScreen
          currentUrl={currentUrl}
          setCurrentUrl={setCurrentUrl}
          logs={logs}
          setLogs={setLogs}
        />
      )}
    </div>
  );
}

export default Terminal;
