interface TopPannelProps {
  leftPannelToggle: boolean;
  setLeftPannelToggle: React.Dispatch<React.SetStateAction<boolean>>;
  bottomPannelToggle: boolean;
  setBottomPannelToggle: React.Dispatch<React.SetStateAction<boolean>>;
  rightPannelToggle: boolean;
  setRightPannelToggle: React.Dispatch<React.SetStateAction<boolean>>;
}

import { IoSearchSharp } from "react-icons/io5";
import { BiDockLeft } from "react-icons/bi";
import { BiSolidDockLeft } from "react-icons/bi";
import { BiDockBottom } from "react-icons/bi";
import { BiSolidDockBottom } from "react-icons/bi";
import { BiDockRight } from "react-icons/bi";
import { BiSolidDockRight } from "react-icons/bi";
import { logToTerminal } from "../terminal/logToTerminal/logToTerminal";
function TopPannel(topPannelProps: TopPannelProps) {
  return (
    <div className="w-screen h-8 bg-bg fixed  rounded-t-xl z-10">
      <div className="w-screen flex justify-between items-center align-middle  mt-1.5">
        <div className="w-14 h-5 ml-2 flex  items-center justify-evenly">
          <div className="h-2.5 w-2.5 bg-red-500 rounded-4xl" />
          <div className="h-2.5 w-2.5 bg-yellow-400 rounded-4xl" />
          <div className="h-2.5 w-2.5 bg-green-700 rounded-4xl" />
        </div>
        <div className="w-[30rem] flex justify-center text-gray-100 text-[0.8rem] items-center  rounded-sm h-5 opacity-80">
          Vaibhav Kapoor - Portfolio
        </div>
        <div className="w-20 h-5 mr-4 flex justify-between items-center">
          <div></div>
          <div
            onClick={() => {
              logToTerminal("left pannel toggle clicked!");
              topPannelProps.setLeftPannelToggle(
                !topPannelProps.leftPannelToggle
              );
            }}
          >
            {topPannelProps.leftPannelToggle ? (
              <BiSolidDockLeft className="text-white text-xl flex " />
            ) : (
              <BiDockLeft className="text-white text-xl flex" />
            )}
          </div>
          <div
            onClick={() => {
              logToTerminal("bottom pannel toggle clicked!");
              topPannelProps.setBottomPannelToggle(
                !topPannelProps.bottomPannelToggle
              );
            }}
          >
            {topPannelProps.bottomPannelToggle ? (
              <BiSolidDockBottom className="text-white text-xl  flex" />
            ) : (
              <BiDockBottom className="text-white text-xl  flex" />
            )}
          </div>
          <BiDockRight className="text-white text-xl  flex" />
        </div>
      </div>
    </div>
  );
}

export default TopPannel;
